@import url(https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap);
html, body {
    width: 100vw;
    height: 100vh;
    padding: 0 auto;
    margin: 0 auto;
    background: linear-gradient(rgb(161, 203, 231), rgb(77, 153, 224));
    background: linear-gradient(rgb(199, 240, 17),rgb(23, 182, 174));
    /* background-image: url('./bg.png'); */
    background-size: cover;
    font-family: 'Fjalla One', sans-serif;
}

#container {
    border-radius: 1rem;
    display: flex;
    align-items: center;
    align-self: auto;
    flex-direction: column;
    text-align: center;
    color: white;
    position: absolute;
    top: 35%;
    left: 50%;
    transform: translate(-50%, -35%);
    width: calc(30vh + 30vw);
    box-shadow: 0 0 2vh rgba(0, 0, 0, 0.534);
}

#title {
    margin: 3rem 0 0.5rem 0;
    font-size: calc(1rem + 2.5vw);
}

#container {
    background: linear-gradient(rgb(81, 0, 255),rgb(255, 0, 76));
    text-align: center;
   
}



#subtitle {
    margin: 0.7rem;
    margin-top: -0.3rem;
}

#generate {
    width: calc(10vw + 4rem);
    font-weight: bold;
    margin: 1rem 0 2rem 0;
    outline: none;
    background-color: rgb(0, 0, 0);
    color: white;
    border: none;
    padding: 0.5rem;
    border-radius: 30rem;
}

.animated {
    -webkit-animation: click 0.4s;
            animation: click 0.4s;
}

@-webkit-keyframes click {
    0% {
       box-shadow: none;
    }

    50% {
        box-shadow: white 0 0 1rem;
    }

    100% {
        box-shadow: none;
    }
}

@keyframes click {
    0% {
       box-shadow: none;
    }

    50% {
        box-shadow: white 0 0 1rem;
    }

    100% {
        box-shadow: none;
    }
}
#result {
    margin: 2vw;
    background: white;
    display: flex;
    color: black;
    flex-direction: column;
    align-items: center;
    width: 100%;
    /* border-radius: 0 0 1rem 1rem; */


    
}

#resultText {
    width: calc(20vh + 20vw);
    font-size: calc(1rem + 0.85vw);
    margin: 1.5rem 0 0.5rem 0;
}
#chart {
    margin-top: 4vh;
    background: white;
    display: flex;
    color: black;
    flex-direction: column;
    align-items: center;
    width: 100%;
    border-radius: 0 0 1rem 1rem;


    
}

#chart p {
    margin: 0 auto;
}
